'use client';

import Link from 'next/link';
import { FaBars, FaTimes } from 'react-icons/fa';
import { useState, useEffect } from 'react';
import { usePathname } from 'next/navigation';

const Navbar = () => {
  const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false);
  const [isScrolled, setIsScrolled] = useState(false);
  const pathname = usePathname();

  // Check if the current path matches the link
  const isActive = (path: string) => {
    return pathname === path;
  };

  const toggleMobileMenu = () => {
    setIsMobileMenuOpen(!isMobileMenuOpen);
  };

  // Close mobile menu when clicking a link
  const closeMobileMenu = () => {
    setIsMobileMenuOpen(false);
  };

  // Handle scroll effect
  useEffect(() => {
    const handleScroll = () => {
      setIsScrolled(window.scrollY > 20);
    };
    
    window.addEventListener('scroll', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  }, []);

  // Close mobile menu when window is resized beyond mobile breakpoint
  useEffect(() => {
    const handleResize = () => {
      if (window.innerWidth >= 768) {
        setIsMobileMenuOpen(false);
      }
    };
    
    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  const navigationItems = [
    { href: '/', label: 'Home' },
    { href: '/about', label: 'JIVO ENERGY' },
    { href: '/projects', label: 'Projects' },
    { href: '/esg', label: 'ESG' },
    { href: '/csr', label: 'CSR' },
    { href: '/media', label: 'Media' },
    { href: '/gallery', label: 'Gallery' },
    { href: '/careers', label: 'Careers' }
  ];

  // Split navigation items into two arrays
  const leftNavItems = navigationItems.slice(0, 4);
  const rightNavItems = navigationItems.slice(4);

  return (
    <nav className={`fixed top-0 w-full z-40 transition-all duration-500 ${
      isScrolled ? 'bg-[#0A5C35]/80 backdrop-blur-lg shadow-lg' : 'bg-transparent'
    }`}>
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="flex items-center justify-end h-20">
          {/* Mobile menu button */}
          <button 
            className="md:hidden relative z-50 p-2 rounded-lg hover:bg-[#4ADE80]/10 transition-all duration-300"
            onClick={toggleMobileMenu}
            aria-label="Toggle mobile menu"
          >
            <div className={`transition-all duration-300 ${isMobileMenuOpen ? 'opacity-100 rotate-180' : 'opacity-0'} absolute inset-0 flex items-center justify-center`}>
              <FaTimes size={24} className="text-[#4ADE80]" />
            </div>
            <div className={`transition-all duration-300 ${isMobileMenuOpen ? 'opacity-0 -rotate-180' : 'opacity-100'}`}>
              <FaBars size={24} className="text-[#4ADE80]" />
            </div>
          </button>
        </div>
      </div>

      {/* Desktop Navigation */}
      <div className="hidden md:block fixed inset-0 z-40 pointer-events-none">
        <div className="absolute inset-0 flex justify-between">
          {/* Left Navigation */}
          <div className="flex flex-col items-start justify-center pl-16 space-y-20">
            {leftNavItems.map(({ href, label }) => (
              <Link 
                key={href}
                href={href}
                className={`relative text-3xl font-light tracking-wider transition-all duration-500 group font-poppins pointer-events-auto ${
                  isActive(href) 
                    ? 'text-black translate-x-4' 
                    : 'text-white/90 hover:text-black hover:translate-x-4'
                }`}
              >
                <span className="relative z-10 uppercase">{label}</span>
                <span className={`absolute -left-6 top-1/2 -translate-y-1/2 w-1.5 h-1.5 rounded-full bg-black transition-all duration-500 ${
                  isActive(href) ? 'opacity-100' : 'opacity-0 group-hover:opacity-100'
                }`} />
              </Link>
            ))}
          </div>

          {/* Right Navigation */}
          <div className="flex flex-col items-end justify-center pr-16 space-y-20">
            {rightNavItems.map(({ href, label }) => (
              <div key={href} className="w-full flex justify-end">
                <Link 
                  href={href}
                  className={`relative text-3xl font-light tracking-wider transition-all duration-500 group font-poppins pointer-events-auto ${
                    isActive(href) 
                      ? 'text-black -translate-x-4' 
                      : 'text-white/90 hover:text-black hover:-translate-x-4'
                  }`}
                >
                  <span className="relative z-10 uppercase">{label}</span>
                  <span className={`absolute -right-6 top-1/2 -translate-y-1/2 w-1.5 h-1.5 rounded-full bg-black transition-all duration-500 ${
                    isActive(href) ? 'opacity-100' : 'opacity-0 group-hover:opacity-100'
                  }`} />
                </Link>
              </div>
            ))}
          </div>
        </div>
      </div>

      {/* Mobile menu */}
      <div 
        className={`fixed inset-0 z-40 bg-[#0A5C35]/95 backdrop-blur-lg transition-all duration-500 ease-in-out transform ${
          isMobileMenuOpen ? 'translate-x-0 opacity-100' : 'translate-x-full opacity-0 pointer-events-none'
        }`}
      >
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 pt-20">
          <div className="flex flex-col space-y-6">
            {navigationItems.map(({ href, label }) => (
              <Link 
                key={href}
                href={href}
                className={`px-6 py-4 rounded-xl text-xl font-light tracking-wider transition-all duration-500 font-poppins ${
                  isActive(href)
                    ? 'text-black bg-[#4ADE80]/10 translate-x-4'
                    : 'text-white/90 hover:text-black hover:bg-[#4ADE80]/10 hover:translate-x-4'
                }`}
                onClick={closeMobileMenu}
              >
                <span className="uppercase">{label}</span>
              </Link>
            ))}
          </div>
        </div>
      </div>
    </nav>
  );
};

export default Navbar; 